import React from 'react';
import * as MESSAGE from "../constants/messageContants";

class CartItem extends React.Component {
    deleteCartItem = (cart) => {
        this.props.onDeleteCart(cart);
        this.props.onMessageDelete(MESSAGE.MSG_DELETE_CART_SUCCESS);
    }
    updateCartItem = (product, value) => {
        this.props.onUpdateCart(product, value);
        this.props.onMessageUpdate(MESSAGE.MSG_UPDATE_CART_SUCCESS);
    }
    render() {
        const { cart } = this.props;
        
        return (
            <tr>
                <th scope="row">
                    <img src={ cart.product.image }
                        alt="" className="img-fluid z-depth-0" />
                </th>
                <td>
                    <h5>
                        <strong>{ cart.product.name }</strong>
                    </h5>
                </td>
                    <td>
                        { cart.product.price }$
                    </td>
                <td className="center-on-small-only">
                    <span className="qty">
                        { cart.quantity }
                    </span>
                    <div className="btn-group radio-group" data-toggle="buttons">
                        <label
                            onClick={ () => this.updateCartItem(cart.product ,-1) }
                            className="btn btn-sm btn-primary btn-rounded waves-effect waves-light">
                            <a href="/#">—</a>
                        </label>
                        <label
                            onClick={ () => this.updateCartItem(cart.product ,1) }
                            className="btn btn-sm btn-primary btn-rounded waves-effect waves-light">
                            <a href="/#">+</a>
                        </label>
                    </div>
                </td>
                <td>
                    { cart.product.price * cart.quantity }$
                </td>
                <td>
                    <button
                        onClick={ () => this.deleteCartItem(cart.product) }
                        type="button" className="btn btn-sm btn-primary waves-effect waves-light" data-toggle="tooltip" data-placement="top"
                        title="" data-original-title="Remove item">
                        X
                    </button>
                </td>
            </tr>
        );
    }
}

export default CartItem;
