import React from 'react';
import * as MESSAGE from "../constants/messageContants";

class App extends React.Component {
    showStarRate = (rating) => {
        let result = [];
        for(let i = 0 ; i < rating ; i++){
            result.push(
                <li key={i} ><i className="fa fa-star"></i></li>
            )
        }
        for(let i = 0 ; i < 5 - rating ; i++){
            result.push(
                <li key={rating + i} ><i className="fa fa-star-o"></i></li>
            )
        }
        return result;
    }
    buyProduct = (product) => {
        this.props.onAddToCart(product);
        this.props.onMessageAdd(MESSAGE.MSG_ADD_CART_SUCCESS);
    }

    render() {
        const { product } = this.props;
        const star = product.rate;
        
        return (
            <div className="col-lg-4 col-md-6 mb-r">
                <div className="card text-center card-cascade narrower">
                    <div className="view overlay hm-white-slight z-depth-1">
                        <img src={product.image}
                            className="img-fluid" alt="" />
                        <a href="/#">
                            <div className="mask waves-light waves-effect waves-light"></div>
                        </a>
                    </div>
                    <div className="card-body">
                        <h4 className="card-title">
                            <strong>
                                <a href="/#">
                                    {product.name}
                                </a>
                            </strong>
                        </h4>
                        <ul className="rating">
                            { this.showStarRate(star) }
                        </ul>
                        <p className="card-text">
                            {product.description}
                        </p>
                        <div className="card-footer">
                            <span className="left">
                                {product.price}$
                            </span>
                            <span className="right">
                                <a 
                                    onClick = { () => this.buyProduct(product) }
                                    href="#/"
                                    className="btn-floating blue-gradient"
                                    data-toggle="tooltip"
                                    data-placement="top" title=""
                                    data-original-title="Add to Cart">
                                    <i className="fa fa-shopping-cart"></i>
                                </a>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default App;
