import React from 'react';
import { connect } from 'react-redux';
import * as MESSGAGE from '../constants/messageContants';
import * as action from "../action/index";
import Carts from "../components/carts";
import CartItem from "../components/cartItem";
import CartResult from  "../components/cartResult";
import PropTypes from 'prop-types';

class CartContainer extends React.Component {
    showCart = (carts) => {
        let result = null;
        if(carts.length > 0){
            result = carts.map( (cart,index) => {
                return  <CartItem
                            key={index}
                            cart={cart}
                            onDeleteCart = {this.props.onDeleteCart}
                            onMessageDelete = {this.props.onMessageDelete}
                            onUpdateCart = {this.props.onUpdateCart}
                            onMessageUpdate = {this.props.onMessageUpdate}
                        />
            })
        }
        else{
            result = <tr><td>{MESSGAGE.MSG_EMPTY_CART}</td></tr>
        }
        return result;
    }
    showTotalAmount = (carts) => {
        const total = carts.reduce( (sum,cart) => {
            return sum += cart.product.price * cart.quantity
        },0);
        return <CartResult total={total} />;
    }

    render() {
        const { carts } = this.props;
        return (
            <Carts>
                { this.showCart(carts) }
                { this.showTotalAmount(carts) }
            </Carts>
        );
    }
}

CartContainer.propTypes = {
    carts : PropTypes.arrayOf(
        PropTypes.shape({
            product: PropTypes.shape({
                id: PropTypes.number.isRequired,
                name: PropTypes.string.isRequired,
                image: PropTypes.string.isRequired,
                description: PropTypes.string.isRequired,
                price: PropTypes.number.isRequired,
                inventory: PropTypes.number.isRequired,
                rate: PropTypes.number.isRequired
            }),
            quantity: PropTypes.number.isRequired
        })
    ).isRequired,
    onDeleteCart: PropTypes.func.isRequired,
    onUpdateCart: PropTypes.func.isRequired,
    onMessageDelete: PropTypes.func.isRequired,
    onMessageUpdate: PropTypes.func.isRequired
}

const mapStateToProps = (state) => {
    return {
        carts: state.carts
    }
}
const mapDispatchToProps = (dispatch, props) => {
    return{
        onDeleteCart: (product) => {
            dispatch(action.deleteCart(product))
        },
        onMessageDelete: (message) => {
            dispatch(action.messageDelete(message))
        },
        onUpdateCart : (product, value) => {
            dispatch(action.updateCart(product, value))
        },
        onMessageUpdate : (message) => {
            dispatch(action.messageUpdate(message))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps) (CartContainer);
