import * as types from "../constants/actionType";

export const addToCart = (product, quantity) => {
    return {
        type: types.ADD_TO_CART,
        product : product,
        quantity: quantity,
    }
}
export const deleteCart = (product) => {
    return{
        type: types.DELETE_CART,
        product : product
    }
}
export const updateCart = (product, value) => {
    return{
        type: types.UPDATE_CART,
        product : product,
        value : value
    }
}
export const messageAdd = (mess) => {
    return{
        type: types.MESSAGE_ADD,
        message: mess
    }
}
export const messageDelete = (mess) => {
    return{
        type: types.MESSAGE_DELETE,
        message: mess
    }
}
export const messageUpdate = (mess) => {
    return{
        type: types.MESSAGE_UPDATE,
        message: mess
    }
}